import NearbyOfoMap, {ofoMapType} from './componnets/NearbyOfoMap'
export default {
  name: 'home-screen',
  components: {NearbyOfoMap},
  data () {
    return {
      ofoMapType: ofoMapType.NEARBY_CAR
    }
  },
  async created () {
    try {
      await this.$store.dispatch('asyncGetUnFinishedOrderNo')
    } catch (e) {
      console.error(e)
    }
  }
}



